import React, { Component } from 'react';

import '../App.css';

class LeftButton extends Component {
  constructor(props) {
    super(props);
    this.state = { 
         left:"",
       }
  }

  render() {

  fetch('https://stormy-ravine-51277.herokuapp.com/values',{
      method: 'post',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify({  
        up:"",
        down:"",
        left : this.state.left,
        right:"",
      })
    })

  const leftStart = () =>{
      this.setState({left: "True"});
    }

  const leftStop = () =>{
      this.setState({left: "False"});
    }
    
  
  return(
     <div  >
      <button onMouseDown={leftStart} onMouseUp={leftStop} onTouchStart={leftStart} onTouchEnd={leftStop} className="Mrgn-btn" type="button" style={{fontSize:30}} >&#8592;</button>
     </div>
    );
  }
}
export default LeftButton;
