import React, { Component } from 'react';

import '../App.css';

class UpButton extends Component {
  constructor() {
    super();
    this.state = { 
         up:"",
       }
  }

  render() {
  	
  	fetch('https://stormy-ravine-51277.herokuapp.com/values',{
      method: 'post',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify({
        up : this.state.up,
        down:"",
        left:"",
        right:"",
      })
    })
  
  const upStart = () =>{
    this.setState({up : "True"});    
  }

  const upStop = () =>{
      this.setState({up: "False"});
    }
  
  return(
   <div className="Common" >
      <button onMouseDown={upStart} onMouseUp={upStop} onTouchStart={upStart} onTouchEnd={upStop} className="Mrgn-btn" type="button" style={{fontSize:30}}>&#8593;</button>
   </div>
  	);																										
  }
}
export default UpButton;
