import React, { Component } from 'react';

import '../App.css';

class DownButton extends Component {
  constructor(props) {
    super(props);
    this.state = { 
         down:"",
       }
  }

  render() {

   fetch('https://stormy-ravine-51277.herokuapp.com/values',{
      method: 'post',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify({
        up:"",
        down : this.state.down,
        left : "",
        right:"",
      })
    })

  const downStart = () =>{
    this.setState({down: "True"});    
  }

  const downStop = () =>{
      this.setState({down: "False"});
    }
 
  return(
    <div className="Common" >
      <button onMouseDown={downStart} onMouseUp={downStop} onTouchStart={downStart} onTouchEnd={downStop} className="Mrgn-btn" type="button" style={{fontSize:30}} >&#8595;</button>
    </div>
     );
  }
}
export default DownButton;
