import React, { Component } from 'react';

import '../App.css';

class RightButton extends Component {
  constructor(props) {
    super(props);
    this.state = { 
         right:"",
       }
  }

  render() {

  fetch('https://stormy-ravine-51277.herokuapp.com/values',{
      method: 'post',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify({
        up:"",
        down:"",
        left:"",
        right : this.state.right,
      })
    })

  const rightStart = () =>{
      this.setState({right: "True"});
    }

  const rightStop = () =>{
     this.setState({right: "False"});
    }


  return(
    <div className="Common" >
       <button onMouseDown={rightStart} onMouseUp={rightStop} onTouchStart={rightStart} onTouchEnd={rightStop} className="Mrgn-btn" type="button" style={{fontSize:30}} >&#8594;</button>
    </div>
  );
  }
}
export default RightButton;
